<template>
  <div>
    <ul class="list-group">
      <li class="list-group-item clearfix" v-for="user of users" :key="user.id">
        <span class="float-left">{{user.fullName}} [роль: {{user.role}}, тарифный план: {{user.tariff}}]</span>
        <button type="button" class="btn btn-outline-danger float-right" @click="deleteUser(user.id)">Удалить</button>
      </li>
    </ul>
  </div>
</template>

<script>
import Axios from '@/axios'
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data(){
    return{
      users: []
    }
  },
  methods: {
    async deleteUser(id){

      const rowDeleted = (await Axios.delete(`users/${id}`)).data

      if(rowDeleted){
        this.users = this.users.filter(user => user.id!==id)
      }

    }
  },
  async created(){
    this.users = (await Axios.get('users')).data
  }
}
</script>
